import FeatureArea from '../Home/FeatureAreas';
import Footer from '../Home/Footer';


export default class Layout extends React.Component{
    render(){
        return(
            <div>
            <Navbar/>
            <TeamBanner/>
            <MyTeam/>
            <FeatureArea/>
            <Footer/>
            </div>
        );
    }
}