import React from 'react';
import Navbar from '../Home/NavBar';
import PropertyBanner from './PropertyBanner'; 
import CityArea from '../Home/CityAreas';
import PropertiesArea from '../Home/PropertiesArea';
import FeatureArea from '../Home/FeatureAreas';
import Footer from '../Home/Footer';


export default class Layout extends React.Component{
    render(){
        return(
            <div>
            <Navbar/>
            <PropertyBanner/>
            <PropertiesArea/>
            <FeatureArea/>
            <CityArea/>
            <Footer/>
            </div>
        );
    }
}