import React from 'react';

export default class HomeBanner extends React.Component{
    render(){
        return(
            <div>
            <section className="home_banner_area">
            <div className="banner_inner d-flex align-items-center">
				<div className="container">
					<div className="banner_content">
						<h5>The joy of home owning</h5>
						<h3>Find Your New Home</h3>
						<a className="main_btn" href="#">Learn More</a>
					</div>
				</div>
            </div>
            <div className="container">
				<div className="advanced_search">
					<h3>Search Properties for</h3>
					<div className="search_select">
						<select className="s_select">
							<option value="1">Choose Locations</option>
							<option value="2">Property Type</option>
							<option value="4">Bedrooms</option>
						</select>
						<select className="s_select">
							<option value="1">Property Type</option>
							<option value="2">Choose Locations</option>
							<option value="4">Bedrooms</option>
						</select>
						<select className="s_select">
							<option value="1">Bedrooms</option>
							<option value="2">Property Type</option>
							<option value="4">Choose Locations</option>
						</select>
						<select className="s_select">
							<option value="1">Bathrooms</option>
							<option value="2">Property Type</option>
							<option value="4">Bedrooms</option>
						</select>
					</div>
					<div className="search_range">
						<div className="range_item">
							<h5>Price Range</h5>
							<div id="slider-range"></div>
							<span className="d_text">$200</span>
							<input type="text" id="amount" readOnly style={{border:0}} className="amount"></input>

						</div>
						<div className="range_item">
							<h5>property Area</h5>
							<div id="slider-range2"></div>
							<span className="d_text2">50sqm</span>
							<input type="text" id="amount2" readOnly style={{border:0}} className="amount2"></input>
                    	</div>
					</div>
					<button type="submit" value="submit" className="submit_btn">Search Property</button>
				</div>
            </div>
        </section>
            </div>
        );
    }
}