import React from 'react';
import Navbar from '../Home/NavBar';
import HomeBanner from './AboutBanner'; 
import WelcomArea from '../Home/WelcomeArea';
import TestimonialArea from '../Home/TestimonialArea';
import FeatureAreas from '../Home/FeatureAreas';
import ClientLogo from '../Home/ClientLogo';
import Footer from '../Home/Footer';


export default class Layout extends React.Component{
    render(){
        return(
            <div>
            <Navbar/>
            <HomeBanner/>
            <WelcomArea/>
            <FeatureAreas/>
            <TestimonialArea/>
            <ClientLogo/>
            <Footer/>
            </div>
        );
    }
}