import React from 'react';

export default class MyTeam extends React.Component{
    render(){


        const mystyle = {
            display: "block",
            marginLeft: "auto",
            marginRight: "auto",
            width: "50%",
                    
          };
        return(
            <section className="team_area p_120">
        	<div className="container">
        		<div className="row team_inner">
                    <div className="col-md-3 col-sm-3" style={{width:"50%",
                    marginLeft:"auto",marginRight:"auto",height:"50%"}}>
        				<div className="team_item">
        					<div className="team_img">
                                <img className="img-fluid" 
                                src="assets/img/team/team-1.jpg" alt=""></img>
        						<div className="hover">
        								<a href="https://www.linkedin.com/in/talha-dar-495082153/">
                                        <i className="fa fa-linkedin"></i></a>
        						</div>
        					</div>
        					<div className="team_name">
        						<h4>Talha Dar</h4>
        						<p>Web Developer</p>
        					</div>
        				</div>
        			</div>
        			
        			
        			
        		</div>
        	</div>
        </section>
        );
    }
}